













export default {
	data: {
		goods_id: 0,
		goods_info: [],
		cover: '',
		disabled: false,
		plain: false,
		loading: false,
		primarySize: 'default',
		type: 'primary',
		nodes: ""
	},
	onLoad: function(option) {
		this.goods_id = option.goodsid;
		this.reset();
		this.jiazai();
		uni.setStorage({
			key: 'storage_key',
			data: 'hello',
			success: function() {
				console.log('success');
			}
		});
	},
	components: {},
	methods: {
		jiazai: function() {
			var _this = this;
			wx.request({
				url: 'https://www.slluks.com/index.php/app/goods/goodsinfo',
				header: {
					'content-type': 'application/json' // 默认值
				},
				data: {
					'goodsid': this.goods_id,
				},
				success: function(res) {
					_this.goods_info = res.data;
					_this.cover = _this.goods_info.img_temp_array[0].pic_cover;
					_this.nodes= _this.goods_info['description'];
				}
			});
		},
		addtocar: function() {
			uni.showToast({
				title: '添加购物车成功',
				duration: 1000,
				icon: 'none'
			});
			this.type = 'warn';
			this.disabled = true;
		},
		reset: function() {
			this.type = 'primary';
			this.disabled = false;
		}
	},
	onPullDownRefresh() {
		this.reset();
		uni.showToast({
			title: '刷新成功',
			duration: 1000,
			icon: 'none'
		});
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	}
}



